<script>
import data from "@/data.js";
import map from "@/map/map.js";
var shieldPost = (data, shieldPostId) => {
  try {
    if (localStorage.getItem('shieldPostId'))
      var shieldPostId = JSON.parse(localStorage.getItem('shieldPostId'))
  } catch (e) {
    var shieldPostId = undefined
  }
  if (!shieldPostId) return data;
  data.forEach(item => {
    if (shieldPostId.includes(item.id))
      item.filterLevel = 2
  })
  return data;
}
const hotExplainContent=` <span>『火锅底料』社区热度计算公式:</span> <br /> <span>热度 = 浏览量 × 1 + 评论数 × 2 + 点赞数 × 3 + 收藏数 × 4 - 举报数 × 5</span> <br /> <span>热度排名由上往下取前十</span> <span></span>`
export default {
  name: "hot",
  data() {
    return {
      tab: 0,
      map: map,
      HotPostRecently: [],
      digestPosts: [],
      snackbar: {
        show: false,
        message: "再点一次退出",
        color: "grey-darken3",
      },
      bottomDrawer: {
        showBottomDrawer: false,
        chooseIndex: 0,
        dislike_num: 0,
        listName: 'HotPostRecently'
      },
      hotExplainDialog: false,
      hotExplainContent:sessionStorage.getItem('hotExplainContent')||hotExplainContent
    };
  },
  created() {
    let that = this;
    that.$request('get', '/forum/getHotPostRecently', {}, {})
      .then(res => {
        shieldPost(res.data)
        that.HotPostRecently = res.data
      })
      .catch(e => {
        that.showSnackbar(e)
      })
    setTimeout(() => {
      that.$request('get', '/forum/getDigestPosts', {}, {})
        .then(res => {
          shieldPost(res.data)
          that.digestPosts = res.data
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    }, 500);
  },
  methods: {
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = ''
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date.toJSON().substr(0, 16).replace('T', ' ');
      }
      return res;
    },
    likeOrDislike(i, value, listName) {
      let that = this;
      that.$request('post', '/forum/forumLikeOrDislike', {}, {
        postId: Number(that[listName][i].id),
        value
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
          if (value == 1) that[listName][i].like_num++;
          else if (value == -1) {
            that.bottomDrawer.dislike_num++;
            that[listName][i].dislike_num++;
          }
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
        })
    },
    addCollect(i, listName) {
      let that = this;
      that.$request('post', '/forum/addCollect', {}, {
        postId: Number(that[listName][i].id),
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e);
        })
    },
    shieldPost(i,listName) {
      let postId = this[listName][i].id;
      try {
        if (localStorage.getItem('shieldPostId')) {
          let shieldPostId = JSON.parse(localStorage.getItem('shieldPostId'))
          if (shieldPostId.includes(postId)) {
            this.showSnackbar('已经屏蔽过了')
            return;
          }
          shieldPostId.push(postId)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!', 'ok');
          this[listName].splice(i, 1);
        }
        else {
          let shieldPostId = []
          shieldPostId.push(postId)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!', 'ok');
          this[listName].splice(i, 1);
        }
      } catch (e) {
        localStorage.removeItem('shieldPostId')
        this.showSnackbar('屏蔽失败!')
      }
    },
    showBottomDrawer(i, listName) {
      this.bottomDrawer.showBottomDrawer = true
      this.bottomDrawer.chooseIndex = i
      this.bottomDrawer.dislike_num = this[listName][i].dislike_num
      this.bottomDrawer.listName = listName
    }
  },
  watch: {
    'hotExplainDialog'(v) {
      this.$store.change_dialog(v, 'hotExplainDialog')
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'hotExplainDialog')
        this.hotExplainDialog = v
    }
  }
};
</script>

<style>
.main_container {
  width: 100vw;
  margin-top: 60px;
}
.item_container {
  background-color: #f5f5f5;
}
.filter {
  margin-left: 85vw;
}
</style>



